.header {
  position: fixed;
  height: 90px;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1;
  transition: .3s ease-in;
  overflow: hidden;
  background-color: #0B0B0B;
  font-family: 'Roboto', sans-serif;


}

.header .navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1240px;
  margin: auto;
  height: 100%;
  padding: 0 1rem;
}
.header .nav-menu a {
  /* color: #231f20; */
  color: #f8f8f8;
  font-size: 24px;
}
.header .navbar img {
  width: 120px;
  height: auto;
}
.header .nav-menu {
  display: flex;
}
.header .nav-item {
  padding: 1rem;
  font-weight: 500;
}
.nav-item .active {
  padding-bottom: 12px;
  border-bottom: 3px solid #ED1C24;
}
.nav-item a:hover {
  padding-bottom: 12px;
  border-bottom: 3px solid #ED1C24;
}
.hamburger {
  display: none;
}


@media screen and (max-width: 940px) {
  .header  {
    max-width: 100%;
    
  }
  .header .navbar {
    max-width: 100%;
  }
  .hamburger {
    display: block;
  }
  .nav-menu {
    position: fixed;
    left: -100%;
    top: 90px;
    flex-direction: column;
    background-color:#0B0B0B;
    width: 100%;
    height: 90vh;
    z-index: 999;
    text-align: center;
    transition: .3s;
  }
  .nav-menu.active {
    left: 0;
  }
  .nav-item {
    margin: 1.5 0;
  }
  .header .navbar img {
    width: 80px;
  }
}